import React, { useEffect, useState } from 'react';
import { HeadCate, TopicList, Pagination } from './components';
import './cnode.scss';
import $api from '@/api';

const columns = [
  {
    dataIndex: 'img',
    key: 'img',
  },

  {
    dataIndex: 'tab',
    key: 'tab',
    width: '1rem',
  },
  {
    dataIndex: 'content',
    key: 'content',
  },
  {
    dataIndex: 'time',
    key: 'time',
    width: '1.5rem',
  },
];
const tabList = [
  {
    id: 1,
    label: '全部',
    value: '',
  },
  {
    id: 2,
    label: '精华',
    value: 'good',
  },
  {
    id: 3,
    label: '分享',
    value: 'share',
  },
  {
    id: 4,
    label: '问答',
    value: 'ask',
  },
  {
    id: 5,
    label: '招聘',
    value: 'job',
  },
];
const Cnode = function (props) {
  console.log(props);
  const [list, setList] = useState([]);
  const [params, setParams] = useState({
    tab: '',
    page: 1,
    limit: 20,
    mdrender: true,
  });
  const changeTab = (tab) => {
    setParams({ ...params, tab });
  };
  const onListChange = (page, limit) => {
    setParams({ ...params, page, limit });
  };
  useEffect(() => {
    $api.requestList(params)
      .then(
        ({ data }) => {setList(data);},
      );
  }, [params]);
  return (
    <div className="cnode">
      <HeadCate value={params.tab} onChange={changeTab} list={tabList} />
      <TopicList
        tab={params.tab}
        tabList={tabList}
        list={list.map((item) => ({
          id: item.id,
          img: item.author.avatar_url,
          reply: item.reply_count,
          visit: item.visit_count,
          top: item.top,
          good: item.good,
          tab: item.tab,
          title: item.title,
          time: item.last_reply_at,
        }))}
        columns={columns}
      />
      <Pagination
        showSizeChanger
        onShowSizeChange={onListChange}
        onChange={onListChange}
        defaultCurrent={1}
        total={500}
      />
    </div>
  );
};
export default Cnode;
