import React from 'react';
import {
  string,
  arrayOf,
  shape,
  func,
  number,
} from 'prop-types';
import { Tag } from 'antd';

const { CheckableTag } = Tag;
const HeadCate = function ({ value, onChange, list }) {
  return (
    <div className="head-cate">
      {
        list.map((ele) => (
          <CheckableTag
            checked={value === ele.value}
            value={ele.value}
            color="red"
            onChange={(_) => _ && onChange(ele.value)}
            key={ele.id}
          >
            {ele.label}
          </CheckableTag>
        ))
      }
    </div>
  );
};
HeadCate.propTypes = {
  value: string.isRequired,
  list: arrayOf(shape({
    id: number.isRequired,
    label: string.isRequired,
    value: string.isRequired,
  })).isRequired,
  onChange: func.isRequired,
};
export default HeadCate;
