import moment from 'moment';

moment.updateLocale('zh-cn', {
  meridiem: function (hour, minute, isLowercase) {
    if (hour < 9) {
      return '早上';
    } if (hour < 11 && minute < 30) {
      return '上午';
    } if (hour < 13 && minute < 30) {
      return '中午';
    } if (hour < 18) {
      return '下午';
    }
    return '晚上';

  },
});
export default moment;
