import React, { Component } from 'react';
import { Table } from 'antd';
import {
  string,
  arrayOf,
  shape,
  number,
  bool,
} from 'prop-types';
import timer from '@/utils/timer';

const TopicList = function (props) {
  const {
    columns, list, tab, tabList,
  } = props;
  return (
    <div className="topic-list">
      <Table
        className="list-table"
        showHeader={false}
        pagination={false}
        columns={columns}
        dataSource={
          list.map((item) => (
            {
              key: item.id,
              img: <img className="avatar-img" src={item.img} alt="" />,
              content: (
                <>
                  <div className="ellipsis list-title">
                    {item.title}
                  </div>
                  <div>
                    <span className="">{item.reply}</span>
                    /
                    <span>{item.visit}</span>
                  </div>
                </>
              ),
              tab: (
                item.top
                  ? '顶置'
                  : item.good
                    ? '精华'
                    : tab
                      ? ''
                      : tabList.find((tab) => tab.value === item.tab)?.label
              ),
              time: timer(item.time).startOf('hour').fromNow(),
            }
          ))
        }
      />
    </div>
  );
};
TopicList.defaultProps = {
  columns: [{ dataIndex: '', key: '' }],
  tabList: [{ id: 1, label: '全部', value: '' }],
  tab: '',
};
TopicList.propTypes = {
  columns: arrayOf(
    shape(
      {
        dataIndex: string.isRequired,
        key: string.isRequired,
      },
    ),
  ),
  list: arrayOf(
    shape(
      {
        id: string.isRequired,
        img: string.isRequired,
        reply: number.isRequired,
        visit: number.isRequired,
        top: bool.isRequired,
        good: bool.isRequired,
        tab: string,
        title: string.isRequired,
        time: string.isRequired,
      },
    ),
  ).isRequired,
  tab: string,
  tabList: arrayOf(
    shape(
      {
        id: number.isRequired,
        label: string.isRequired,
        value: string.isRequired,
      },
    ),
  ),
};
export default TopicList;
